<template>
  <div class="tk-px-margin-bottom-px0" style="height: calc(100vh - 90px)">
    <el-table
      class="table-responsive align-items-center table-flush tk-table-max-height"
      header-row-class-name="thead-light"
      :data="lessonTypes"
      id="lessonTypesList"
    >
      <el-table-column
        label="Instrument"
        min-width="140px"
        prop="name"
        sortable
      >
        <template v-slot="{ row }">
          <span
            class="avatar avatar-sm rounded-circle tk-layout-inline-block tk-cursor-pointer tk-bg-color-gray-200"
            @click.prevent="preview(row)"
          >
            <img :src="getInstrumentIcon(row)" />
          </span>
          <span
            class="tk-layout-inline-block tk-px-padding-left-px10 tk-text-overflow text-sm tk-cursor-pointer"
            style="width: calc(100% - 36px)"
            @click.prevent="preview(row)"
          >
            <b>{{ getInstrumentName(row) }}</b>
          </span>
        </template>
      </el-table-column>

      <el-table-column
        label="Lesson Type"
        min-width="160px"
        prop="name"
        sortable
      >
        <template v-slot="{ row }">
          <div class="text-sm tk-cursor-pointer" @click.prevent="preview(row)">
            <b>{{ row.name }}</b>
          </div>
          <div
            class="tk-font-color-lightGray text-sm tk-cursor-pointer"
            @click.prevent="preview(row)"
          >
            {{ row.timeLength }}min, {{ formatPrice(row) }}
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import bus from "@/bus.js";
import commons from "@/util/commons.js";
import $ from "jquery";
import {
  Table,
  TableColumn,
  DropdownMenu,
  DropdownItem,
  Dropdown,
  Tooltip,
} from "element-ui";

export default {
  name: "inline-actions-table",
  components: {
    [Tooltip.name]: Tooltip,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
    [Dropdown.name]: Dropdown,
    [DropdownItem.name]: DropdownItem,
    [DropdownMenu.name]: DropdownMenu,
  },
  props: {
    lessonTypes: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data() {
    return {
      instruments: []
    };
  },
  created() {
    this.instruments = this.$tools.instruments
  },
  mounted() {
    let self = this;
    self.initClick()
    bus.$on("clearSelected",() => {
      $(".tk-selected").removeClass("tk-selected");
    })
  },
  methods: {
    initClick() {
      let self = this;

      let lessonTypesList = document.getElementById("lessonTypesList");
      let ul_child = lessonTypesList.getElementsByClassName("el-table__row");
      for (var i = 0; i < ul_child.length; i++) {
        let index = i;
        ul_child[index].onclick = (ev) => {
          if (ev.target.localName !== "button") {
            $(".tk-selected").removeClass("tk-selected");
            ul_child[index].classList.add("tk-selected");
          }
        };
      }

    },
    formatPrice(row) {
      return commons.formatPrice(row.price)
    },
    getInstrumentIcon(row) {
      let url = ''
      this.instruments.some(item => {
        if (parseInt(item.id) === parseInt(row.instrumentId)) {
          url = item.url
        }
      })
      return url
    },
    getInstrumentName(row) {
      let name = ''
      this.instruments.some(item => {
        if (parseInt(item.id) === parseInt(row.instrumentId)) {
          name = item.name
        }
      })
      return name
    },
    onEdit(row) {
      //   alert(`You want to edit ${row.name}`);
      alert(`You want to edit`);
    },
    preview(data) {
      this.$emit("preview", data);
    },
  },
};
</script>
